@import "variables";

.thumbnail {
  position: relative;
  border-radius: 0;
  color: $font-gray;
  padding: 10px;
  height: 150px;
  margin-bottom: 0;
  outline: none;
  .name {
    padding: 0 0 0 0;
    height: 20px;
    text-align: center;
    overflow: hidden;
  }
  .version {
    color: $font-blue;
    text-align: center;
    font-size: $version-font-size;
  }
  &:hover {
    border: 1px solid $light—blue;
  }
}

.fr-thumbnail-new-tag {
  position: absolute;
  left: -3px;
  top: -3px;
  height: 46px;
  width: 46px;
  background-image: url($image-path + "/img/icon_new.png");
}

.fr-thumbnail-disable-tag {
  position: absolute;
  left: -3px;
  top: -3px;
  height: 46px;
  width: 46px;
  background-image: url($image-path + "/img/icon_disable.png");
}

.fr-thumbnail-unreachable-tag {
  position: absolute;
  left: -3px;
  top: -3px;
  height: 46px;
  width: 46px;
  background-image: url($image-path + "/img/icon_unreachable.png");
}

.fr-thumbnail-disable-new-tag {
  position: absolute;
  left: -3px;
  top: -3px;
  height: 46px;
  width: 46px;
  background-image: url($image-path + "/img/icon_disable-new.png");
}

.fr-thumbnail-marked {
  position: absolute;
  right: -1px;
  top: -1px;
  height: 25px;
  width: 25px;
  background-image: url($image-path + "/img/icon_marked.png");
}

.fr-thumbnail-selected-border {
  border: 1px solid $light—blue;
}

$tooltip-head-height: 30px;
$tooltip-footer-height: 60px;
$red: #ef6156;

.tooltip {
  &.left {
    top: 5px !important;
    padding: 0 !important
  }
  &.right {
    top: 5px !important;
    padding: 0 !important
  }
  &.bottom-left {
    top: 160px !important;
    left: inherit !important;
    right: 5px !important;
    padding: 0 !important;
  }
  &.bottom-right { //借用来展示下方向左甩的样式
    top: 160px !important;
    left: 5px !important;
    padding: 0 !important
  }
  &.in { //坑爹的内嵌浏览不支持透明..
    opacity: 1;
  }
  .fr-tooltip {
    position: relative;
    width: 250px;
    background-color: $white;
    border: 1px solid $light—blue;
    color: $white;
    .head-left {
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      padding: 6px 10px 6px 10px;
      height: $tooltip-head-height;
      background-color: $light—blue;
    }
    .head-right {
      position: absolute;
      right: -1px;
      top: -1px;
      height: $tooltip-head-height + 1;
      padding: 7px 10px 6px 10px;
      background-color: $red;
    }
    .content {
      position: relative;
      padding: $tooltip-head-height + 15 10px $tooltip-footer-height + 10 10px;
      line-height: 25px;
      color: $font-gray;
      .moreInfo {
        position: absolute;
        right: 10px;
        bottom: $tooltip-footer-height + 10;
        height: 20px;
        width: 50px;
        background-image: url($image-path + "/img/icon_more_normal.png");
        cursor: hand;
        color: $white;
        &:hover {
          background-image: url($image-path + "/img/icon_more_hover.png");
        }
      }
    }
    .footer {
      position: absolute;
      border-top: 1px solid $border-gray;
      padding: 10px;
      left: 0;
      bottom: 0;
      width: 100%;
      height: $tooltip-footer-height;
      color: $a-hover;
      div {
        line-height: 20px;
      }
    }
  }
}
