@import "variables";

.fr-search {
  position: relative;
  border-radius: $search-radius;
  margin: 10px 0 10px;
  &:hover,
  &:focus {
    .fr-search-img {
      background-image: url($designer-path + "/img/icon_search_hover.png");
    }
    .fr-search-query {
      outline: none;
      border: 1px solid $hover-search-border-color;
    }
  }
  .fr-search-img {
    background-image: url($designer-path + "/img/icon_search_normal.png");
    position: absolute;
    height: 30px;
    width: 30px;
    left: 0;
    top: 0;
  }
  .fr-search-query {
    padding: 5px 0 5px 30px;
    word-break: keep-all;
    white-space: nowrap;
    overflow: hidden;
    margin-right: 5px;
    outline: none;
    border-radius: $default-radius;
    height: 30px;
    width: $search-width;
    color: $nor-search-border-color;
    background-color: white;
    border: 1px solid $nor-search-border-color;
  }
}