@import "variables";

%small-btn {
  padding: 5px;
  background-color: $light—blue;
  border-radius: $default-radius;
  color: $white;
  outline: none;
  &:hover, &:focus {
    background-color: $font-blue;
  }
}

//分类导航区
.nav-group {
  position: relative;
  padding: 5px;
  .row {
    position: relative;
    &.last-row .items-head {
      padding: 21px 7px 21px 7px;
    }
    .items-head {
      position: absolute;
      padding: 6px 7px 6px 7px;
      background-color: $pale-blue;
      font-size: $font-size;
      color: $font-blue;
    }
    .head-line {
      height: 1px;
      width: 30px;
      background-color: $white;
      position: absolute;
      top: 29px;
      left: 5px;
    }
    .items-body {
      padding: 0 0 0 60px;
      height: 30px;
      .items {
        height: 30px;
        padding: 6px 0 6px;
        border-bottom: 1px solid rgba(0, 0, 0, 0.05);
        &.extra-items {
          border-bottom: none;
        }
        .item {
          @extend %small-btn;
          background-color: $panel-background;
          color: $font-gray;
          &:hover, &:focus {
            background-color: $btn-background;
            color: $a-hover;
          }
        }
      }
    }
  }
}

.selected-item {
  background-color: $btn-background !important;
  color: $a-hover !important;
}

//导航状态区
.status-bar {
  height: 50px;
  .tags {
    padding: 12px 6px 13px 6px;
    color: $font-blue;
    > .status-item {
      @extend %small-btn;
      position: relative;
      margin: 0 10px 0 0;
      padding: 4px 19px 3px 5px;
      > .tag-close {
        position: absolute;
        top :8px;
        right :5px;
        height: 9px;
        width: 9px;
        cursor: pointer;
        background-image: url($image-path + "/img/icon_close9x9_normal.png");
      }
      &:hover {
        > .tag-close {
          cursor: pointer;
          background-image: url($image-path + "/img/icon_close9x9_hover.png");;
        }
      }
    }
    > .recommend-item{
      padding: 4px 5px 3px 5px;
      border-radius: $default-radius;
      background-color: $panel-background;
      color: $font-blue;
      margin: 0 10px 0 0;
    }
  }
}