@import "variables";

%fr-btn-group {
  background-color: $btn-group-color;
  border-top: 1px solid $btn-group-line;
  width: 100%;
  height: 50px;
}

.btn-group {
  padding: 10px;
  margin: 0 0 0 5px;

}

button {
  height: 30px;
  padding: 5px;
  margin-right: 5px;
  background-color: $btn-background-color;
  color: $btn-font-color;
  border: none;
  border-radius: $btn-radius;
  float: left;
  font-weight: normal;
  &:hover {
    background-color: $hover-btn-background-color
  }
  &:disabled {
    background-color: $disable-btn-background-color;
    color: $disable-btn-font-color;
  }
}

button[disabled] {
  background-color: $disable-btn-background-color;
  color: $disable-btn-font-color;
}

#delete-plugin-btn {
  @extend button;
  background-color: $red-btn-background;
  &:hover {
    background-color: $red-hover-btn-background;
  }
  &:disabled {
    background-color: $disable-btn-background-color;
  }
}

#popup_message {
  margin: 8px !important;
}

#install-from-disk, #update-from-disk {
  opacity: 0;
  filter: alpha(opacity=0);
  cursor: pointer;
  position: absolute;
  z-index: 101;
  width: 0px;
  height: 0px;
  right: 25px;
  bottom: 15px;
}
