@import "variables";
@import "titlebar";
@import "search";
@import "btnAndGroup";
@import "thumbnail";
@import "progress";
@import "scrollbar";
@import "navs";
@import "disconnected";
@import "login";
@import "compatible_style";

/*
以下是一些常用的全局定义
*/
a {
  color: $font-gray;
  &:hover, &:focus {
    color: $a-hover;
    text-decoration: none;
  }
}

* {
  box-sizing: border-box;
  font-size: $font-size;
  font-family: "Myriad Pro", "OpenSans", Verdana, "PingFang SC", "Microsoft YaHei", sans-serif;
}

img {
  max-width: 100%;
  height: auto;
}

/*
下面是各个组件的样式
*/
.scrollable {
  overflow: auto;
}

.container-fluid {
  margin: 0 $margin-s 0 $margin-s;
  padding: 0;
  .row {
    margin: 0;
    padding: 0;
  }
}

//主栏
.mainView {
  float: left;
  width: 64.77%;
  position: relative;
  padding: $margin-l $margin-s $margin-l $margin-s;
  .panel {
    border: none;
    margin-bottom: 0;
    .panel-heading {
      height: 30px;
      padding: 6px 15px 8px 15px;
      background-color: $pale-blue;
      color: $font-blue;
      border-bottom: 0;
      border-top-left-radius: 4px;
      border-top-right-radius: 4px;
      .login {
        float: right;
        margin: 0 85px 0 0;
        display: block;
        .loginInfo {
          background-color: deepskyblue;
          border-radius: 0;
          text-align: center;
          margin: -5px 0 0 0;
          width: 100px;
          height: 30px;
          position: absolute;
        }
      }
    }
    .panel-body {
      position: relative;
      height: 600px;
      padding: 15px;
      background-color: $panel-background;
      .loginBtn {
        right: 0;
        display: none;
        z-index: 1;
        position: absolute;
        width: 100px;
        height: 80px;
        background-color: white;
        margin: 0 0 0 435px;
        border: 1px solid #D7E4E4;
        top: 0;
        .bbs-msg {
          height: 25px;
          width: 80px;
          margin: 10px 0 0 10px;
          background-color: deepskyblue;
        }
        .loginIn {
          height: 25px;
          width: 80px;
          margin: 10px 0 0 10px;
          background-color: deepskyblue;
        }
      }
    }
  }
}

.product-row {
  width: 100%;
  margin: 0;
  height: 480px;
  overflow-y: scroll;
  .col-sm-3 {
    padding: 5px;
  }
}

.product-row .col-sm-2, .sideView .installed-row .col-sm-2 {
  width: 20%;
  padding: 5px;
}

/* add by kerry */
.sideView .installed-row .col-sm-4 {
  padding: 5px
}

.product-board {
  position: relative;
  .transition {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 80px;
    height: 80px;
    margin: -40px 0 0 -40px;
    z-index: 10;
    background-image: url($image-path + "/img/loading.gif");
  }
}

.blur-loading {
  filter: blur(2px);
  -webkit-filter: blur(2px);
}

//侧栏
.sideView {
  @extend .mainView;
  float: left;
  width: 35.23%;
  position: relative;
  border: 1px;
  .fr-search {
    margin: 0 0 10px 5px;
  }
  .installed-count {
    color: $nor-search-border-color;
    padding: 6px 5px 15px 0;
  }
  .installed-row {
    @extend .product-row;
    height: 480px;
    .col-sm-6 {
      padding: 5px;
    }
  }
}

//按钮组
.install-btn-group, .installed-btn-group {
  @extend %fr-btn-group;
  position: absolute;
  border-color: #ddd;
  z-index: 100;
  left: 0;
  bottom: 0;
  border-bottom-left-radius: $default-radius;
  border-bottom-right-radius: $default-radius;
}


