@import "variables";

.dialog-close {
  position: absolute;
  right: 0;
  top: 0;
  width: 30px;
  height: 100%;
  cursor: pointer;
  background-image: url($image-path + "/img/icon_close40x40_normal.svg");
}
.title-bar {
  top: 0;
  width: 100%;
  position: relative;
  height: $title-bar-height;
  background-color: $font-blue;
  color: white;
  padding: 6px 0 8px 20px;
  .dialog-close {
    position: absolute;
    right: 0;
    top: 0;
    width: $title-bar-height;
    height: 100%;
    cursor: pointer;
    background-image: url($image-path + "/img/icon_close40x40_normal.svg");
    &:hover{
      background-image: url($image-path + "/img/icon_close40x40_hover.svg");
    }
  }
}