/* Let's get this party started */
::-webkit-scrollbar {
  width: 6px;
}

/* Track */
::-webkit-scrollbar-track {
  -webkit-border-radius: 10px;
  border-radius: 10px;
}

/* Handle */
::-webkit-scrollbar-thumb {
  -webkit-border-radius: 3px;
  border-radius: 3px;
  background: rgba(0,0,0,0.05);
  &:hover{
    background: rgba(0,0,0,0.1);
  }

  &:window-inactive{
    background: rgba(0,0,0,0.05);
  }
}